@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
body.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #2f2b3a;
  color: #f8f8f8;
  padding-top: 10%;
}
body.homepage h1 {
  margin: 0px 0 0 0;
  font-size: 44px;
}
body.homepage label {
  font-size: 24px;
}
body.homepage form {
  display: flex;
  flex-direction: column;
  margin: 10px;
}
body.homepage form input {
  padding: 8px;
  margin: 4px 0;
}
body.homepage form input:focus {
  outline: none;
  box-shadow: none;
}
body.homepage form input[type=submit] {
  width: 50%;
  margin-left: 25%;
}

body {
  font-family: "Roboto";
}